import React from "react";
import image1 from "../Image/villa.jpg";
import { Box, Grid, GridItem, Text } from "@chakra-ui/react";

const About = () => {
  return (
    <>
      <Grid
        h="750px"
        templateRows="repeat(2, 1fr)"
        templateColumns="repeat(2, 1fr)"
        gap={0}
      >
        <GridItem colSpan={1}>
          <div style={{ height: "100%" }}>
            <h1 className="About-h1">A propos</h1>
            <Box
              borderBottom="15px solid yellow"
              borderRadius="10"
              width="100px"
              ml={10}
              mb={4}
            ></Box>
            <Text className="About-p">
              {" "}
              Notre socit, Yasaph Construction, est un pilier dans l'industrie
              du Btiment et des Travaux Publics depuis sa fondation en 2020.
              Forts de notre engagement envers l'excellence et la qualit, nous
              nous spcialisons dans la ralisation de projets de construction
              d'envergure, de rnovations innovantes et de la cration
              d'infrastructures durables. Notre quipe hautement qualifie et
              notre approche mticuleuse garantissent des rsultats
              exceptionnels pour nos clients. Chez Yasaph Construction, nous
              nous efforons de dpasser les attentes, en mettant l'accent sur
              la collaboration troite avec nos clients, tout en respectant les
              normes de scurit et d'environnement les plus strictes.
            </Text>
          </div>
        </GridItem>

        <GridItem colSpan={1}>
          <div style={{ backgroundColor: "lightgreen", height: "100%" }}>
            <img src={image1} alt="villa" className="AboutImage"></img>
          </div>
        </GridItem>

        <GridItem rowSpan={1} colSpan={2}>
          <div
            style={{
              backgroundColor: "Yellow",
              height: "100%",
              paddingLeft: "50",
            }}
          >
            <Grid
              h={100}
              templateColumns="repeat(4, 1fr)"
              paddingLeft={200}
              paddingRight={200}
              paddingTop={3}
            >
              <div className="AB">
                <h1 className="h1AB">2019</h1>
                <p>Anne de cration</p>
              </div>
              <div className="AB">
                <h1 className="h1AB">54</h1>
                <p>Projets</p>
              </div>
              <div className="AB">
                <h1 className="h1AB">27</h1>
                <p>Contrats</p>
              </div>
              <div className="AB">
                <h1 className="h1AB">15</h1>
                <p>Personnel</p>
              </div>
            </Grid>
          </div>
        </GridItem>
      </Grid>
    </>
  );
};

export default About;
